.room-container {
    padding: 20px;
    margin-left: 260px; /* prevent overlap with sidebar */
  }
  
  .room-form {
    display: flex;
    gap: 10px;
    margin-bottom: 30px;
    position: sticky;
    top: 0;
    background: white;
    padding: 10px;
    z-index: 100;
  }
  
  .room-form input {
    padding: 8px;
    font-size: 14px;
    width: 200px;
  }
  
  .room-form button {
    padding: 10px 20px;
    background-color: #8b008b;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .room-form button:hover {
    background-color: #006b8a;
  }

  .room-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .room-card {
    width: 250px;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 1px 2px 5px #8b008b;
  }
  
  .room-card h3 {
    margin: 0;
  }
  
  .room-card p {
    color: #555;
  }
  
  .room-card-buttons {
    margin-top: 10px;
    display: flex;
    gap: 10px;
  }
  
  .room-card-buttons button {
    padding: 6px 12px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .room-card-buttons button:first-child {
    background-color: #8b008b;
    color: white;
  }
  .room-card-buttons:hover button:first-child:hover {
    background-color: #006b8a;
    color: white;
  }
  
  .room-card-buttons button:last-child {
    background-color: #8b008b;
    color: white;
  }
  